<template>
    <div class="fh-input-group" :class="{'has-error': hasError}">
        <label class="fh-input-label" :class="{active: !!value}">{{ label }}</label>
        <input class="fh-input-field" :type="type" :placeholder="label" :value="value" @input="$emit('input', $event.target.value)">
        <span v-show="hasError" class="fh-input-message">{{ errorMessage }}</span>
    </div>
</template>

<script>
export default {

    props: {
        value: {
            default: null,
            type: String
        },
        type: {
            default: "text",
            type: String
        },
        label: {
            default: null,
            type: String
        },
        hasError: {
            default: false,
            type: Boolean
        },
        errorMessage: {
            default: "Value not valid.",
            type: String
        }
    }
}
