<template>
    <div class="fh-page fh-alert-modal" :class="{open: is_open}">
        <div class="fh-page-head">
            <div class="fh-close-button" @click="onClose">
                <svg width="14" height="14" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M11.0833 3.73916L10.2608 2.91666L6.99996 6.17749L3.73913 2.91666L2.91663 3.73916L6.17746 6.99999L2.91663 10.2608L3.73913 11.0833L6.99996 7.82249L10.2608 11.0833L11.0833 10.2608L7.82246 6.99999L11.0833 3.73916Z" fill="black"/>
                </svg>
            </div>
        </div>
        <div class="fh-page-body">
            <h1 class="fh-second-title">Product Suggestion</h1>
            <h2 class="fh-second-sub-title">Why not try this instead?</h2>
            <div style="height: 50px"></div>
            <div class="fh-comparison-table" v-if="item_a != null && item_b != null">
                <div class="fh-row">
                    <div class="fh-col full">
                        <fh-product-item :item="item_a" ></fh-product-item>
                    </div>
                    <div class="fh-col flex-center">
                        <span class="fh-versus-text">VS</span>
                    </div>
                    <div class="fh-col full flex-end">
                        <fh-product-item :item="item_b" ></fh-product-item>
                    </div>
                </div>
                <div class="fh-row" style="align-items: flex-start">
                    <div class="fh-col full">
                        <table class="fh-table-details">
                            <tr>
                                <th class="red">You</th>
                                <th class="green">Planet</th>
                            </tr>

                            <tr v-for="el in getOrderedData(item_a.nutrients)" :key="el.id">
                                <td>{{ el.name.toUpperCase() }}</td>
                                <td>{{ el.amount }} {{el.unit_of_measure }}</td>
                            </tr>

                            <tr>
                                <td>CO2 IMPACT</td>
                                <td style="font-weight: bold;font-size: 20px">{{ get_co2_impact(item_a) }}</td>
                            </tr>
                        </table>
                    </div>
                    <div class="fh-col flex-center"></div>
                    <div class="fh-col full flex-end">
                        <table class="fh-table-details">
                            <tr>
                                <th class="red">You</th>
                                <th class="green">Planet</th>
                            </tr>

                            <tr v-for="el in getOrderedData(item_b.nutrients)" :key="el.id">
                                <td>{{ el.name.toUpperCase() }}</td>
                                <td>{{ el.amount }} {{el.unit_of_measure }}</td>
                            </tr>

                            <tr>
                                <td>CO2 IMPACT</td>
                                <td style="font-weight: bold;font-size: 20px">{{ get_co2_impact(item_b) }}</td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
        </div>
        <div class="fh-page-footer">
            <fh-button @action="toSwitchProduct">Switch Item</fh-button>
            <span @click="toAddToCart" class="fh-modal-btn-link">No thanks, keep the same items</span>
            <div style="height: 30px"></div>
        </div>
    </div>
</template>

<script>
import BtnVue from '@/components/Btn.vue';
import ProductItemVue from '../ProductItem.vue';
import { mapActions } from 'vuex';

const CO2_IMP = {
    'a': '',
    'b': '',
    'c': '',
    'd': '',
    'e': '',
};

export default {

    props: {
        item_a: {
            default: null,
            type: Object
        },
        item_b: {
            default: null,
            type: Object
        },
        is_open: {
            default: false,
            type: Boolean
        }
    },

    components: {
        'fh-button': BtnVue,
        'fh-product-item': ProductItemVue
    },

    methods: {

        ...mapActions({
            addProduct: 'cart/addProduct',
            addReplacement: 'cart/addReplacement',
        }),

        onClose() {
            this.$emit('onClose');
        },
        toSwitchProduct() {
            this.addProduct(this.item_b);

            this.addReplacement({
                a_id: this.item_a.id,
                b_id: this.item_b.id,
            });

            this.$emit('onClose');
        },
        toAddToCart() {
            this.addProduct(this.item_a);
            this.$emit('onClose');
        },

        get_co2_impact(element) {
            return CO2_IMP[element.nutri_score_final.toLowerCase()];
        },
        getOrderedData(list) {
            return list.sort((a, b) => (a.name > b.name) ? 1 : -1);
        }
    }
}
</script>

