<template>
    <div class="fh-page fh-payment-page">
        <div class="fh-page-head">
            <fh-back-button @action="$router.go(-1)"></fh-back-button>
        </div>
        <div class="fh-page-body">
            <div class="fh-container">
                <h1 class="fh-title fh-txt-center">Payment</h1>
                <fh-input-field type="text" label="Card Number" v-model="payment.card_number"></fh-input-field>
                <fh-input-field type="text" label="Name on card" v-model="payment.name_on_card"></fh-input-field>
                <fh-input-field type="text" label="Expiry" v-model="payment.expiry"></fh-input-field>
                <fh-input-field type="text" label="CVC" v-model="payment.cvc"></fh-input-field>
            </div>
        </div>
        <div class="fh-page-footer">
            <fh-button @action="toSubmit">Pay {{ parseFloat(total).toFixed(2) }} </fh-button>
        </div>
    </div>
</template>

<script>
import Btn from '@/components/Btn.vue';
import BackButton from '@/components/BackButton.vue';
import InputField from '@/components/fields/InputField.vue';
import { mapActions } from 'vuex';
export default {

    props: {
        total: {
            default: 0,
            type: Number
        }
    },

    data: () => ({
        payment: {
            card_number: null,
            name_on_card: null,
            expiry: null,
            cvc: null,
        }
    }),

    components: {
        'fh-button' : Btn,
        'fh-back-button' : BackButton,
        'fh-input-field' : InputField,
    },

    methods: {

        ...mapActions({
            resetCart: 'cart/resetCart',
        }),

        toSubmit() {
            this.$router.push({name: 'home'}).then( () => { this.resetCart(); });
        }
    }



}
</script>

