<template>
    <div class="fh-page fh-page-auth">
        <div class="fh-auth-head"></div>
        <div class="fh-auth-body">
            <div class="fh-container">
                <fh-back-button @action="$router.go(-1)"></fh-back-button>
                <h1 class="fh-title">Sign In</h1>
                <div style="height: 20px"></div>
                <fh-input-field type="email" label="Email" v-model="login.email"></fh-input-field>
                <fh-input-field type="password" label="Password" v-model="login.password"></fh-input-field>
                <div style="height: 40px"></div>
                <fh-button @action="toLogin">Sign In</fh-button>
                <p class="fh-description">Forgot password? <router-link class="fh-link" :to="{name: 'lost'}">Recover it</router-link></p>
            </div>
        </div>
    </div>
</template>

<script>
import Btn from '@/components/Btn.vue';
import BackButton from '@/components/BackButton.vue';
import InputField from '@/components/fields/InputField.vue';

import { HTTP } from "../../http-common";
import { mapActions } from 'vuex';

export default {

    data: () => ({
        login: {
            email: null,
            password: null,
        }
    }),

    components: {
        'fh-button' : Btn,
        'fh-back-button' : BackButton,
        'fh-input-field' : InputField,
    },

    methods: {

        ...mapActions({
            fetchProfile: 'user/fetchProfile'
        }),

        toLogin() {
            HTTP.post('login', this.login)
                .then( res => {
                    let jwt = res.data.data.access_token;
                    localStorage.setItem('fh_token', jwt);
                })
                .then(this.fetchProfile)
                .then(() => { this.$router.push({name: 'home'}); })
                .catch ( error => {
                    console.log(error);
                    alert(JSON.stringify(error));
                });
        }
    }
}
</script>

