<template>
    <div class="fh-choices">
        <fh-choice @action="toChoice" :active="current == 'lose_weight'" label="lose weight" value="lose_weight"></fh-choice>
        <fh-choice @action="toChoice" :active="current == 'gain_weight'" label="gain weight" value="gain_weight"></fh-choice>
        <fh-choice @action="toChoice" :active="current == 'stay_fit'" label="stay fit" value="stay_fit"></fh-choice>
    </div>
</template>

<script>
import ChoiceVue from './Choice.vue';

export default {

    data: () => ({
        current: null
    }),

    components: {
        'fh-choice' : ChoiceVue
    },

    methods: {
        toChoice(choice) {
            this.current = choice;
        }
    }


}
</script>

