<template>
    <button
        :disabled="disabled"
        class="fh-btn"
        :class="{
            animated: tapped
        }"
        @click="onClick">
        <slot></slot>
    </button>
</template>

<script>
export default {
    data: () => ({
        tapped: false,
    }),

    props: {
        disabled: {
            default: false,
            type: Boolean
        }
    },

    methods: {
        onClick(event) {

            this.tapped = true;

            setTimeout(() => {this.tapped = false}, 100);

            this.$emit('action', event);
        }
    }
}
</script>

<style>
    .fh-btn {
        background-color: #DF7551;
        box-shadow: 0px 6px 20px rgba(223, 117, 81, 0.25);
        border-radius: 10px;
        padding: 20px 30px;
        color: white;
        cursor: pointer;
        outline: none;
        border: 1px solid transparent;
        margin-bottom: 30px;
        display: block;
        width: 100%;
        font-family: "Montserrat", sans-serif;
        font-weight: 600;

        transition: all 100ms ease;
    }

    .fh-btn:hover {
        background-color: white;
        color: #DF7551;
        border: 1px solid #DF7551;
    }

    .fh-btn.animated {
        background-color: white;
        color: #DF7551;
        border: 1px solid #DF7551;
    }
</style>
