import Vue from 'vue';
import VueRouter from 'vue-router';

import HomeStore from '../views/HomeStore.vue';
import Comparison from '../views/Comparison.vue';
import Welcome from '../views/Welcome.vue';

// auth components
import Login from '../views/auth/Login.vue';
import Register from '../views/auth/Register.vue';
import Lost from '../views/auth/Lost.vue';
import Done from '../views/auth/Done.vue';

// profile components
import Profile from '../views/Profile.vue';

// redeem components
import Store from '../views/redeem/Store.vue';
import Confirm from '../views/redeem/Confirm.vue';

// cart components
import Cart from '../views/cart/Cart.vue';
import PaymentSuccess from '../views/cart/PaymentSuccess.vue';
import Summary from '../views/cart/Summary.vue';

Vue.use(VueRouter);

const routes = [
  // ROUTES
  {
    path: '/',
    name: 'home',
    component: HomeStore
  },
  {
    path: '/comparison',
    name: 'comparison',
    component: Comparison
  },
  {
    path: '/profile',
    name: 'profile',
    component: Profile
  },
  {
    path: '/welcome',
    name: 'welcome',
    component: Welcome
  },

  // AUTH ROUTES
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '/register',
    name: 'register',
    component: Register
  },
  {
    path: '/lost',
    name: 'lost',
    component: Lost
  },
  {
    path: '/auth-done',
    name: 'done',
    component: Done
  },

  // REDEEMS
  {
    path: '/redeem',
    name: 'redeem',
    component: Store
  },
  {
    path: '/redeem/confirm',
    name: 'redeem-confirm',
    component: Confirm,
    props: true
  },

  // CART
  {
    path: '/cart',
    name: 'cart',
    component: Cart
  },
  {
    path: '/cart/summary',
    name: 'summary',
    component: Summary,
    props: true
  },
  {
    path: '/cart/payment-success',
    name: 'payment-success',
    component: PaymentSuccess,
    props: true
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
