<template>
    <div @click="onClick" class="fh-choices-item" :class="{active: active}">
        <span class="fh-choices-item-label">{{ label }}</span>
    </div>
</template>

<script>
export default {
    props: {
        value: {
            default: null,
            type: String
        },

        active: {
            default: false,
            type: Boolean
        },

        label: {
            default: null,
            type: String
        }
    },

    methods: {
        onClick() {
            this.$emit('action', this.value);
        }
    }
}
</script>

<style>
    .fh-choices-item {
        width: 100px;
        height: 90px;
        background-color: #FCEDE4;
        border: 2px solid #DF7551;
        border-radius: 10px;

        display: flex;
        justify-content: center;
        align-items: center;

        margin-left: 10px;
        cursor: pointer;

        transition: all .3s ease;
    }

    .fh-choices-item:hover {
        background-color: white;
        color: #DF7551;
        box-shadow: 0px 6px 20px rgba(223, 117, 81, 0.25);
    }

    .fh-choices .fh-choices-item:first-of-type {
        margin-left: 0;
    }

    .fh-choices-item.active { background: #DF7551; box-shadow: 0px 6px 20px rgba(223, 117, 81, 0.25);}
    .fh-choices-item.active .fh-choices-item-label { color: white; }

    .fh-choices-item .fh-choices-item-label {
        font-family: "Montserrat", sans-serif;
