import axios from 'axios';
import router from './router';

export const HEADERS = () => ({
	headers: {
		"Authorization": "Bearer " + localStorage.getItem('fh_token')
	}
});

export const HEADERS_FILE = () => ({
	headers: {
		"Authorization": "Bearer " + localStorage.getItem('fh_token'),
		"Content-Type": 'multipart/form-data'
	}
});

const mioAxios = axios.create({
	baseURL: (process.env.NODE_ENV == "development") ? 'http://localhost/api/' : 'http://localhost/api/',
	timeout: 60000,
});

mioAxios.interceptors.response.use(
	response => response,
	error => {
		if(!error.response) {
			return Promise.reject({
				response: {
					data: {
						message: "Nessuna connessione, riprova pi tardi."
					}
				}
			});
		} else {
			if(error.response.data.meta) router.push({name: error.response.data.meta });
			return Promise.reject(error);
		}

	}
);


export const HTTP = mioAxios;
