import { HEADERS, HTTP } from '../../http-common';

const profileSchema = {
    birthday: null,
    created_at: null,
    email: null,
    email_verified_at: null,
    goal: null,
    height: null,
    id: null,
    name: null,
    number_of_meals: null,
    updated_at: null,
    weight: null
};

const insightsSchema = {
    co2_saved_level: null,
    co2_saved_percentage: null,
    money_saved: 0,
    points: 0,
    stats: []
};

const state = {

    profile: {...profileSchema},
    insights: {...insightsSchema}

};

const getters = {
    getProfile: state => state.profile,
    getInsights: state => state.insights,
};

const mutations = {
    unloadProfile: state => { state.profile = {...profileSchema}; },
    setProfile: (state, data) => { state.profile = data; },
    setInsights: (state, data) => { state.insights = data; }
};
const actions = {
    fetchProfile: context => {
        return HTTP.get('/me', HEADERS())
            .then( res => {
                console.log(res);
                context.commit('setProfile', res.data.user);
                context.commit('setInsights', res.data.insights);
                return res.data.user;
            });
    }
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};