<template>
    <div class="fh-page fh-cart-page">
        <div class="fh-page-head">
            <h1 class="fh-title">Cart</h1>
            <h2 class="fh-description"> Find out what the food you bought is <strong>good</strong> for! </h2>
        </div>
        <div class="fh-page-body">
            <div class="fh-cart-section">
                <img class="fh-cart-section-image" :src="require('@/assets/images/cart/sec_1.png')" alt="">
                <div class="fh-cart-section-list">
                    <fh-product-item
                        v-for="(prod, index) in getCartProducts"
                        :key="`prod_${index}_${prod.gtin}`"
                        :item="prod"></fh-product-item>
                </div>
            </div>
            <div class="fh-cart-section">
                <img class="fh-cart-section-image" :src="require('@/assets/images/cart/sec_2.png')" alt="">
                <div class="fh-cart-section-list">
                    <fh-product-item
                        v-for="(prod, index) in shuffle(getCartProducts)"
                        :key="`prod_${index}_${prod.gtin}`"
                        :item="prod"></fh-product-item>
                </div>
            </div>
            <div class="fh-cart-section">
                <img class="fh-cart-section-image" :src="require('@/assets/images/cart/sec_3.png')" alt="">
                <div class="fh-cart-section-list">
                    <fh-product-item
                        v-for="(prod, index) in shuffle(getCartProducts)"
                        :key="`prod_${index}_${prod.gtin}`"
                        :item="prod"></fh-product-item>
                </div>
            </div>
        </div>
        <div class="fh-page-footer">
            <span class="fh-title">Cart</span>
            <span class="fh-total-price">{{ getTotal.toFixed(2) }} </span>
            <fh-button @action="toSubmit">Continue</fh-button>
        </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';
import ButtonVue from '../../components/Btn.vue';
import ProductItemVue from '../../components/ProductItem.vue';
import { HTTP, HEADERS } from '../../http-common';
export default {
    components: {
        'fh-button': ButtonVue,
        'fh-product-item': ProductItemVue,
    },

    computed: {
        ...mapGetters({
            getCartProducts: 'cart/getProducts',
            getCartReplacements: 'cart/getReplacements',
        }),

        getTotal() {
            return this.getCartProducts.reduce((a,b) => b.price + (a.price ? a.price : a))
        }
    },

    methods: {
        shuffle(array) {
            var currentIndex = array.length, temporaryValue, randomIndex;

            // While there remain elements to shuffle...
            while (0 !== currentIndex) {

                // Pick a remaining element...
                randomIndex = Math.floor(Math.random() * currentIndex);
                currentIndex -= 1;

                // And swap it with the current element.
                temporaryValue = array[currentIndex];
                array[currentIndex] = array[randomIndex];
                array[randomIndex] = temporaryValue;
            }

            return array;
        },

        toSubmit() {
            HTTP.post('products', {
                products: this.getCartProducts.map(el => el.id),
                replacements: this.getCartReplacements
            }, HEADERS())
                .then( res => {
                    console.log('submit', res);
                    this.$router.push({name: 'summary', params: {result: res.data.data }})
                })
                .catch(err => console.log('err', err));
        }
    }
}
</script>

