<template>
    <div class="fh-page">
        <div class="fh-page-head fh-row">
            <div class="fh-col full">
                <!-- <fh-search-field></fh-search-field> -->
            </div>
            <div class="fh-col">
                <fh-cart-button></fh-cart-button>
                <fh-avatar-icon></fh-avatar-icon>
            </div>
        </div>
        <div class="fh-page-body fh-container">
            <h1 class="fh-main-title fh-txt-left">Hi, {{ getProfile.name }}!</h1>
            <h2 class="fh-sub-title fh-txt-left">Welcome to the Store</h2>
            <div style="height:50px"></div>
            <fh-section @add-to-cart="addToCart" title="Perfect for your diet!" :list="getProducts.for_diet"></fh-section>
            <fh-section @add-to-cart="addToCart" title="Meat with low Co2 impact!" :list="getProducts.low_co2_impact"></fh-section>
            <fh-section @add-to-cart="addToCart" title="Food for your brain!" :list="getProducts.healthy_food"></fh-section>
        </div>
        <div class="fh-page-footer"></div>
        <fh-alert-modal
            :item_a="item_a"
            :item_b="item_b"
            :is_open="isOpenModalComparison"
            @onClose="closeModalComparison">
        </fh-alert-modal>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import AvatarIconVue from '../components/AvatarIcon.vue';
import CartButtonVue from '../components/CartButton.vue';
import AlertModalVue from '../components/modals/AlertModal.vue';
import SectionVue from '../components/sections/Section.vue';
import { HTTP, HEADERS } from '../http-common';
// import SearchFieldVue from '../components/fields/SearchField.vue';
export default {
    components: {
        'fh-cart-button': CartButtonVue,
        'fh-avatar-icon': AvatarIconVue,
        'fh-section': SectionVue,
        'fh-alert-modal': AlertModalVue,
        // 'fh-search-field': SearchFieldVue,
    },

    data: () => ({
        item_a: null,
        item_b: null,
        isOpenModalComparison: false,
    }),

    computed: {
        ...mapGetters({
            getProfile: 'user/getProfile',
            getProducts: 'products/getProducts'
        })
    },

    methods: {
        ...mapActions({
            fetchProducts: 'products/fetchProducts',
        }),

        addToCart(ev) {
            HTTP.get('products/comparison/' + ev.id, HEADERS())
                .then(res => {
                    console.log(res);
                    this.item_a = res.data.product_a;
                    this.item_b = res.data.product_b;
                    this.isOpenModalComparison = true;
                })
                .catch( err => {
                    console.log(err);
                })
        },

        closeModalComparison() {
            this.isOpenModalComparison = false;
            setTimeout(() => {
                this.item_a = null;
                this.item_b = null;
            }, 500);
        }
    },

    created() {
        this.fetchProducts()
            .then((res) => {
                console.log('fatto', res);
            })
            .catch( err => {
                console.log('errore', err);
            })
    }
}
</script>
