<template>
    <div class="fh-increment-number-wrapper">
        <fh-action @action="toDecrement" type="decrement"></fh-action>
        <input class="fh-increment-number-field" type="text" :value="value" readonly>
        <fh-action @action="toIncrement" type="increment"></fh-action>
    </div>
</template>

<script>
import ActionVue from './Action.vue';

export default {

    props: {
        value: {
            default: 0,
            type: Number
        }
    },

    components: {
        'fh-action': ActionVue
    },

    data: () => ({
        temp: 0,
    }),

    methods: {
        toDecrement() {
            this.temp = this.value;
            this.$emit('input', this.temp > 0 ? this.temp - 1 : 0);
        },
        toIncrement() {
            this.temp = this.value;
            this.$emit('input', this.temp + 1);
        },
    }
}
</script>

<style>
    .fh-increment-number-wrapper {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .fh-increment-number-wrapper .fh-increment-number-field {
        text-align: center;
        width: 100px;
        height: 90px;
        background: #FCEDE4;
        border: 2px solid #DF7551;
        border-radius: 10px;
        margin: 0 20px;
        outline: 0;
        appearance: none;
        font-family: "Montserrat", sans-serif;
        font-size: 30px;
        color: #747474;


    }
</style>
