import { HEADERS, HTTP } from "../../http-common";

const productsSchema = {
    for_diet: [],
    healty_food: [],
    low_co2_impact: [],
};

const state = {
    products: {...productsSchema}
};
const getters = {
    getProducts: (state) => state.products,
};
const mutations = {
    addProduct: (state, {data, section}) => { state.products[section].push(data); },
    initProducts: (state, data) => { state.products = data; },
    resetProducts: (state) => { state.products = {...productsSchema}; },
};
const actions = {
    fetchProducts: (context) => {
        return HTTP.get('products', HEADERS())
            .then( res => {
                context.commit('initProducts', res.data);
                return res.data;
            });
    }
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};