<template>
    <div
        @click="onClick"
        class="fh-avatar-icon"
        :style="avatar ? `background-image: url(${avatar})` : null"
    >
    </div>
</template>

<script>
export default {
    props: {
        avatar: {
            default: null,
            type: String
        }
    },

    methods: {
        onClick() {
            this.$router.push({name: 'profile'});
        }
    }
}
</script>

