const state = {
    products: [],
    replacements: [],
};
const getters = {
    getProducts: state => state.products,
    getReplacements: state => state.replacements,
};

const mutations = {
    addProduct: (state,data) => { state.products.push(data); },
    removeProduct: (state, index) => { state.products.splice(index, 1); },
    resetCart: state => { state.products = []; state.replacements = []; },

    addReplacement: (state, data) => { state.replacements.push({
        product_a_id: data.a_id,
        product_b_id: data.b_id,
    })}
};
const actions = {
    addProduct: (context, data) => { context.commit('addProduct', data); },
    removeProduct: (context, index) => { context.commit('removeProduct', index); },
    resetCart: (context) => { context.commit('resetCart'); },
    
    addReplacement: (context, data) => { context.commit('addReplacement', data); },
};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};