<template>
    <div class="fh-page fh-page-auth">
        <div class="fh-auth-head"></div>
        <div class="fh-auth-body">
            <div class="fh-container">
                <fh-back-button @action="toGoBack"></fh-back-button>
                <h1 class="fh-title">Sign Up</h1>
                <div v-if="step == 1">
                    <div style="height: 20px"></div>
                    <fh-input-field type="text" label="Full Name" v-model="register.fullname"></fh-input-field>
                    <fh-input-field type="email" label="Email" v-model="register.email"></fh-input-field>
                    <fh-input-field type="password" label="Password" v-model="register.password"></fh-input-field>
                </div>

                <div v-else-if="step == 2">
                    <div style="height: 20px"></div>
                    <fh-input-field type="date" label="BirthDate" v-model="register.birtDate"></fh-input-field>
                    <fh-input-field type="text" label="Weight - kg" v-model="register.weight"></fh-input-field>
                    <fh-input-field type="text" label="Height - cm" v-model="register.height"></fh-input-field>
                </div>

                <div v-else-if="step == 3">
                    <h2 class="fh-description">How many meals do you eat each day?</h2>
                    <div style="height: 20px"></div>
                    <fh-increment-number v-model="register.meals"></fh-increment-number>
                </div>

                <div v-else-if="step == 4">
                    <h2 class="fh-description">What is your goal?</h2>
                    <div style="height: 20px"></div>
                    <fh-choices></fh-choices>
                </div>

                <div style="height: 40px"></div>
                <fh-button @action="toNext">{{ this.step >= 4 ? "Complete" : "Next Step" }}</fh-button>
                <div style="height: 40px"></div>
                <fh-step-progress :value="step"></fh-step-progress>
            </div>
        </div>
    </div>
</template>

<script>
import Btn from '@/components/Btn.vue';
import BackButton from '@/components/BackButton.vue';
import InputField from '@/components/fields/InputField.vue';
import StepProgress from '@/components/StepProgress.vue';
import IncrementNumber from '@/components/fields/IncrementNumber/Index.vue';
import Choices from '@/components/fields/Choices/Index.vue';

export default {

    data: () => ({
        register: {
            fullname: null,
            email: null,
            password: null,
            birtDate: null,
            weight: null,
            height: null,
            meals: 0,
        },

        step: 1
    }),

    components: {
        'fh-button' : Btn,
        'fh-back-button' : BackButton,
        'fh-input-field' : InputField,
        'fh-step-progress' : StepProgress,
        'fh-increment-number' : IncrementNumber,
        'fh-choices' : Choices,
    },

    methods: {
        toGoBack() {

            if(this.step <= 1) {
                this.$router.go(-1);
                return;
            }

            this.step--;
        },

        toNext() {
            if(this.step >= 4) {
                this.toSubmit();
                return;
            }
            this.step++;
        },

        toSubmit() {
            alert(JSON.stringify(this.register));
        }
    }
}
</script>

